<template>
    <div class="view-configure">
        <div class="main-content__title"></div>
        <el-form label-width="100px">
            <el-form-item label="">
                <el-input
                        v-model="$dc.configure.proxyPort"
                        placeholder="proxy"
                />
            </el-form-item>
            <el-form-item label="Socks5">
                <el-input
                        v-model="$dc.configure.socks5Port"
                        placeholder="proxy"
                />
            </el-form-item>
            <el-form-item label="">
                <el-input
                        v-model="$dc.configure.requestTimeoutTime"
                        placeholder="proxy"
                />
            </el-form-item>
            <el-form-item>
                <el-button type="primary" @click="saveFile"></el-button>
            </el-form-item>
        </el-form>
    </div>
</template>

<script>
    import confApi from '../../../api/conf';
    import forEach from 'lodash/forEach';

    export default {
        name: 'configure',
        computed: {
            socksProxyExample() {
                return `#
0.0.0.0    # 80 443socks5
4.4.4.4    # 0.0.0.0socks54.4.4.480443`
            }
        },
        methods: {
            async saveFile() {
                this.$dc.configure.requestTimeoutTime = parseInt(
                    this.$dc.configure.requestTimeoutTime
                );
                this.$dc.configure.proxyPort = parseInt(this.$dc.configure.proxyPort);
                this.$dc.configure.socks5Port = parseInt(this.$dc.configure.socks5Port);
                let response = await confApi.saveFile(this.$dc.configure);
                let serverData = response.data;
                if (serverData.code == 0) {
                    this.$message({
                        showClose: true,
                        type: 'success',
                        message: '!'
                    });
                } else {
                    this.$message.error(`${serverData.msg}`);
                }
            }
        }
    };
</script>

<style>
    .view-configure {
        max-width: 500px;
        .el-button {
            margin-top: 15px;
            padding: 10px 28px;
            letter-spacing: 2px;
        }
    }
    .socks-editor {
        height: 500px;
        width: 760px;
        font-size: 18px;
        padding: 10px 20px;
    }
</style>



